import { writable } from "svelte/store";
import { getDefaultLanguage } from "../../shared/util/i18n";
import { storage } from "../util/localStorage";
export var language = writable();
export var defaults = {
    language: getDefaultLanguage()
};
export function init() {
    language.set(storage.getOrElse("language", defaults.language));
    language.subscribe(function (newValue) { return storage.set("language", newValue); });
}
